import React from 'react';
import Slider from "react-slick";
import ApiProvider from "../providers/api_provider";
import 'slick-carousel/slick/slick.css'
import 'slick-carousel/slick/slick-theme.css'
import Vacancy from "./vacancy";

class Vacancies extends React.Component {
    settingsSlider = {
        dots: true,
        infinite: false,
        speed: 500,
        arrows: true,
        slidesToShow: 3,
        slidesToScroll: 1,
    };

    state = {
        pageLoading: true,
        items: [],
    }

    loadData = async () => {
        const items = await ApiProvider.getVacancies();
        this.setState({
            pageLoading: false,
            items: items,
        });
    }

    componentDidMount() {
        this.loadData();
    }

    render() {
        return (
            <div className="vacancy__wrapper">
                <div className="wrapper">
                    <div className="vacancy__title-wrapper">
                        <h2 className="vacancy__title h2">вакансии в гросс маркете</h2>
                    </div>
                    <div className="vacancy__list">
                        {this.state.pageLoading && this.renderLoaders()}
                        {(!this.state.pageLoading && this.state.items.length > 0) && this.renderVacancyItems()}
                    </div>
                </div>
            </div>
        )
    }

    renderVacancyItems = () => {
        const items = [];
        for (const vacancy of this.state.items) {
            items.push(<Vacancy vacancy={vacancy} />)
        }

        return (
            <Slider {...this.settingsSlider}>
                {items}
            </Slider>
        )
    }

    renderLoaders = () => {
        return (
            <Slider {...this.settingsSlider}>
                <div className="vacancy__list-item vacancy__list-item__loader"><div /></div>
                <div className="vacancy__list-item vacancy__list-item__loader"><div /></div>
                <div className="vacancy__list-item vacancy__list-item__loader"><div /></div>
                <div className="vacancy__list-item vacancy__list-item__loader"><div /></div>
                <div className="vacancy__list-item vacancy__list-item__loader"><div /></div>
                <div className="vacancy__list-item vacancy__list-item__loader"><div /></div>
            </Slider>
        )
    }
}

export default Vacancies;